@import url(https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
*,::after,::before{box-sizing:border-box}html,body{overflow:hidden;--body-background-color: #f9f9f9;--body-text-color: #454545;--transition: 0.2s ease-in-out;--font-family: "Mulish", sans-serif;--component-background-color: white;--component-box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.15);padding:0;margin:0;font-size:16px;color:var(--body-text-color);background-color:var(--body-background-color);font-family:var(--font-family)}a{-webkit-text-decoration-line:none;text-decoration-line:none}h1,h2,h3,h4,h5,h6{margin:1%}.title{font-size:20px;font-weight:500;font-stretch:normal;font-style:normal;line-height:normal;letter-spacing:normal;color:#0a0e1c}button{padding:16px 24px;color:#0a0e1c;min-width:120px;font-size:16px;font-weight:400;-webkit-transition:all 0.2s ease-in-out;transition:all 0.2s ease-in-out;background-color:white;outline:none;border:none;text-decoration:none;cursor:pointer;flex:1 1;display:flex;align-items:center;justify-content:center;border-radius:30px;text-align:center}button svg{margin-right:24px}.tag{padding:8px 12px;font-size:14px;border-radius:4px;background-color:#454545;color:white;text-align:center;display:flex;justify-content:center}.tag svg{margin:0 6px}button.blue,.tag.blue{background-color:#0d6efd;color:white}button.blue:hover,.tag.blue:hover{background-color:#024dbc}button.indigo,.tag.indigo{background-color:#6610f2;color:white}button.indigo:hover,.tag.indigo:hover{background-color:#4709ac}button.purple,.tag.purple{background-color:#6f42c1;color:white}button.purple:hover,.tag.purple:hover{background-color:#4e2d89}button.pink,.tag.pink{background-color:#d63384;color:white}button.pink:hover,.tag.pink:hover{background-color:#9d205e}button.red,.tag.red{background-color:#dc3545;color:white}button.red:hover,.tag.red:hover{background-color:#a71d2a}button.orange,.tag.orange{background-color:#fd7e14;color:white}button.orange:hover,.tag.orange:hover{background-color:#c35a02}button.yellow,.tag.yellow{background-color:#ffc107;color:white}button.yellow:hover,.tag.yellow:hover{background-color:#ba8b00}button.green,.tag.green{background-color:#28a745;color:white}button.green:hover,.tag.green:hover{background-color:#19692c}button.teal,.tag.teal{background-color:#20c997;color:white}button.teal:hover,.tag.teal:hover{background-color:#158765}button.cyan,.tag.cyan{background-color:#17a2b8;color:white}button.cyan:hover,.tag.cyan:hover{background-color:#0f6674}button.gray,.tag.gray{background-color:#6c757d;color:white}button.gray:hover,.tag.gray:hover{background-color:#494f54}button.gray-dark,.tag.gray-dark{background-color:#343a40;color:white}button.gray-dark:hover,.tag.gray-dark:hover{background-color:#121416}button.primary,.tag.primary{background-color:#0d6efd;color:white}button.primary:hover,.tag.primary:hover{background-color:#024dbc}button.secondary,.tag.secondary{background-color:#6c757d;color:white}button.secondary:hover,.tag.secondary:hover{background-color:#494f54}button.success,.tag.success{background-color:#28a745;color:white}button.success:hover,.tag.success:hover{background-color:#19692c}button.info,.tag.info{background-color:#17a2b8;color:white}button.info:hover,.tag.info:hover{background-color:#0f6674}button.warning,.tag.warning{background-color:#ffc107;color:white}button.warning:hover,.tag.warning:hover{background-color:#ba8b00}button.danger,.tag.danger{background-color:#dc3545;color:white}button.danger:hover,.tag.danger:hover{background-color:#a71d2a}button.dark,.tag.dark{background-color:#343a40;color:white}button.dark:hover,.tag.dark:hover{background-color:#121416}input,textarea{display:block;width:100%;min-height:calc(1.5em + 0.75rem + 2px);padding:0.375rem 0.75rem;font-size:1rem;font-weight:400;line-height:1.5;color:#495057;background-color:#fff;background-clip:padding-box;max-width:100%;resize:vertical;border:1px solid #ced4da;-webkit-appearance:none;-moz-appearance:none;appearance:none;border-radius:0.25rem;-webkit-transition:border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;transition:border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out}

.header-controller{display:flex;justify-content:flex-end;align-items:center}.header-controller svg{margin:0px 16px}.header-controller .date-time{text-align:right;padding:0 24px}.header-controller .date-time .time{font-size:40px;font-weight:400;text-align:right;color:#0a0e1c;margin-top:12px;margin-bottom:0}.header-controller .date-time .date{font-size:12px;font-weight:500;text-align:right;color:#0a0e1c;margin:0}

.header{display:flex;justify-content:space-between;height:120px;align-items:center}

.search-bar{display:flex;align-items:center;border:1px solid #817878;width:25vw;border-radius:30px;overflow:hidden}.search-bar svg{margin-right:16px;margin-left:20px}.search-bar input{min-height:48px;outline:none;border:none;font-size:20px}.search-bar input::-webkit-input-placeholder{color:#565656}.search-bar input:-ms-input-placeholder{color:#565656}.search-bar input::-ms-input-placeholder{color:#565656}.search-bar input::placeholder{color:#565656}

.todo-container{display:flex}.todo-container .todo-creator{width:50%;padding:24px 24px 24px 0}.todo-container .todo-creator input{padding:12px 30px;border-radius:10px;margin:30px 0;box-shadow:0 0 0 0.05rem rgba(69,69,69,0.14);outline:none}.todo-container .todo-creator input:focus{box-shadow:0 0 0 0.2rem rgba(20,75,255,0.24)}.todo-container .todo-preview{width:50%;padding:24px 0px 24px 24px}.todo-container .todo-preview .todo-wrapper{padding:16px;min-height:300px;background-color:rgba(20,75,255,0.05);overflow:hidden overlay;max-height:550px}.todo-container .todo-preview .todo-wrapper .todo-item{background-color:white;padding:12px 16px;margin-bottom:8px;border-radius:8px;box-shadow:0 0 15px 0 rgba(0,0,0,0.1)}

.container{min-height:100vh;height:100%;display:flex}.container .sidebar{width:240px;height:100%;min-height:100vh;background-color:var(--component-background-color);border-right:1px solid #eee;padding:22px 17.5px}.container .content{flex:1 1;min-height:100vh;height:100%;background-color:var(--component-background-color);padding:40px 60px}

.logo-s{font-size:50px;font-weight:900;font-stretch:normal;font-style:normal;line-height:normal;letter-spacing:normal;color:#000;padding:16px 24px}.btn-side:hover,.btn-side.active{background-color:#0070f8;color:white}

